/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
    padding : 2em;
}
h2 {
    font-size: 1.5em;
    padding : 1em;
}
.app {
    max-width: 1280px;
    margin: auto;
    border: 1px solid black;
    
}

.hideBtn {
    padding: 8px;
    position: absolute;
    left: 10px;
    top: 10px;
}
textarea {
    resize: none;
}
/* buttons */
.edit {
    width: 60px;
    padding: 0.5em;
    position: absolute;
    left: 50px;
    top: 50px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.add {
    width: 60px;
    padding: 0.5em;
    position: absolute;
    left: 50px;
    top: 100px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.delete  {
    width: 60px;
        padding: 0.5em;
        position: absolute;
        left: 50px;
        top: 150px;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
}

/* header */ 
header {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}

.left {
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
    padding: 2em;
}

.infos {
    padding: 2em;
    display: flex;
    flex-direction: column;
    grid-gap : 1em;
    gap : 1em;
}
.nomprenom {
    font-size: 2em;
    display: flex;
    grid-gap: 0.5em;
    gap: 0.5em;
}

/* skills */

.skills {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 200px;
    padding : 0 0 2em 0;
}
h2 {
    width: 70%;
    border-top: 1px solid black;
    text-align: center;
}
.skillList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    width: 70%;
    margin: auto;
    padding: 0.5em;
}
.skillList li {
    text-align: center;
    padding: 0.5em;
}

.education, .jobs {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 200px;
        padding : 0 0 2em 0;
}
.container {
    width: 70%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-gap: 2em;
    gap: 2em;
}
h3 {
    font-weight: bold;
    padding: 0.5em 0;
}
article p {
    display: flex;
    grid-gap : 0.2em;
    gap : 0.2em;
}
p {
    padding: 0.2em 0;
}

.hidden {
    visibility: hidden;
}
